import VisualComponent from "jx/core/comps/VisualComponent";
/** Animation using sequence of Images */
export default class ImageSequence extends VisualComponent {
    constructor(dataObject) {
        super(dataObject);
        this.extendsFrom("ImageSequence");
    }
    /** @type {number} **/
    get framerate() {
        return 0;
    }
    /** @type {number} **/
    set framerate(value) {
    }
    /**
     * [play description]
     * @param  {Object} params
     * @param  {Function} params.onfinished
     */
    play(params = {}) {
    }
    /**
     * Stop and call onfinished
     */
    stop() {
    }
    /**
     * Cancel without call onfinished
     */
    cancel() {
    }
    /**
     * Go to frame.
     * @param  {object} gotoParams
     * @param  {number} gotoParams.frame
     */
    goto(gotoParams) {
        this.cancel();
        this._frame = gotoParams.frame;
        this.view.image = this.imageSequenceResource.data[this._frame].data;
    }
}
